<template>
    <div>
        <v-snackbar v-model="snackbar" :timeout="4000" top color="success">
            <span>{{snackTitle}}</span>

            <template v-slot:action="{ attrs }">
                <v-btn elevation="0" text color="white" v-bind="attrs" @click="snackbar = false">Fechar</v-btn>
            </template>
        </v-snackbar>   
        <h1 class="subtitle-1 light-blue--text text--darken-4"></h1>
        <v-spacer></v-spacer>
        <!-- <v-btn small color="primary" dark class="mb-2" @click="showDialogData(this.meucultoDefault)">Novo culto</v-btn> -->
        <v-card class="mx-auto">
        <v-card-title>
            Meus Cultos
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="mdi-magnify" label="Pesquisa" placeholder="Digite sua pesquisa aqui" single-line hide-details></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="listacultos" :search="search" :items-per-page="-1" :loading="loading" loading-text="Aguarde, carregando dados." 
                      :footer-props="footerprops">
            <template v-slot:top>
            
            <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                    <div align="right">
                        <v-btn color="primary" dark class="mb-2 mr-3" small v-bind="attrs" v-on="on">Novo culto</v-btn>
                    </div>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                        <v-container>                            
                                <v-text-field v-model="meuculto.culto" label="Culto"></v-text-field>
                                <v-text-field v-model="meuculto.vagas" label="Lugares"></v-text-field>
                                <v-row>
                                    <v-col cols="12" sm="6">
                                        <v-menu v-model="menucalendario" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                            <template v-slot:activator="{ on }">
                                                <v-text-field :value="meuculto.data" label="Data" v-on="on" readonly required></v-text-field>                            
                                            </template>    
                                            <v-date-picker v-model="meuculto.data" no-title @input="menucalendario = false" locale="pt-br" ></v-date-picker>
                                        </v-menu>                                        
                                    </v-col>
                                    <v-col cols="12" sm="6">
                                        <v-text-field v-model="meuculto.hora" label="Status" type="time"></v-text-field>
                                    </v-col>                                    
                                </v-row>                                                          
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="error"  @click="close()">Cancelar</v-btn>
                        <v-btn color="primary"  @click="save()">Gravar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>


            <!-- dialog link para exibição do link de convite -->
            <v-dialog v-model="dialogLink" max-width="800px">                
                <v-card>
                    <v-card-title>
                        <span class="headline">Compartilhar convite</span>
                    </v-card-title>

                    <v-card-text>
                        <v-container>                            
                                <v-text-field id="input-link-convite" readonly v-model="link" label="Link do convite"></v-text-field>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="error"  @click="closeLink()">Fechar</v-btn>
                        <v-btn color="success"  @click="copyLink()">Copiar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>



            </template>
            <!-- <template v-slot:item.status="{ item }">
                <v-chip small :color="getColor(item.status)" :class="`v-chip--active white--text caption my-2`">{{item.status}}</v-chip>
            </template>   -->
            <template v-slot:item.data="{ item }">{{ formatDate(item.data) }}</template>  
            <template v-slot:item.status="{ item }">                
                <v-chip small :color="`${getStatusCulto(item.data)}`" :class="`v-chip--active white--text caption my-2`"> {{ getStatusCulto(item.data) }} </v-chip>
                <!-- {{ getStatusCulto(item.data) }} -->
            </template>  

            <template v-slot:item.actions="{item}"> 
                
                <v-icon small class="mr-2" @click="showDialogData(item)">
                    mdi-pencil
                </v-icon>

                <v-icon smal class="mr-2" @click="deleteData(item)">
                    mdi-delete
                </v-icon>
                <v-icon smal class="mr-2" @click="showDialogLinkConvite(item)" >
                    mdi-link
                </v-icon>
            
                <router-link :to="{ name: 'ListaFielAgenda', params: { eventid: item.id }}">
                    <v-icon smal class="mr-2" >
                        mdi-account-details
                    </v-icon>                    
                </router-link>                
                
            </template>
        </v-data-table>
    </v-card>    
    </div>
</template>

<script>    
    import db from '../fb'
    export default {
        data: () => ({
            headers: [
                        { text: 'Culto', align: 'start', sortable: true, value: 'culto', class: 'light-blue--text text--darken-4' },
                        { text: 'Lugares', value: 'vagas', class: 'light-blue--text text--darken-4' },
                        { text: 'Data', value: 'data', class: 'light-blue--text text--darken-4' },
                        { text: 'Hora', value: 'hora', class: 'light-blue--text text--darken-4' },
                        { text: 'Status', value: 'status', class: 'light-blue--text text--darken-4' },
                        { text: 'Acões', value: 'actions', sortable: false, class: 'light-blue--text text--darken-4' }
                    ],
            meuculto: {
                culto: '',
                conteudo: '',
                data: null,
                hora: null,
                vagas: '',
                usuario_id: '',
                status: ''
                
            },
            datacalendario: null,
            menucalendario: false,
            tipoOperacao: -1,
            dialog: false,
            loading: false,
            search: '',
            dialogLink: false,
            link: '',
            snackTitle: '',
            snackbar: '',

            listacultos: [],
            
            footerprops: {
                            'items-per-page-all-text': 'tudo',                            
                            'items-per-page-text': 'Cultos por página',
                            'items-per-page-options': [5, 10, 15, 30, 50, 100, -1]
                        }
        }),

        computed: {
            formTitle () {
                return this.tipoOperacao === -1 ? 'Novo culto' : 'Editar culto'
            },
            computedDateFormatted () {
                return this.dateCalendar ? this.formatDate(this.dateCalendar) : ""            
            }
        },

        methods: {
            

            getColorStatus (status) {
                //console.log(status);
                if (status == 'realizado') return 'green'
                else if (status == 'aberto' ) return 'blue'
                //else if (status = 'cancelado' ) return 'red'
                //else return 'orange'
            },

            getStatusCulto (data){
                var hoje = new Date;
                var ano = hoje.getFullYear();
                var mes = hoje.getMonth();
                var dia = hoje.getDate();
                var datahoje = new Date(ano,mes, dia);
                var data1 = new Date(data).setHours(0,0,0,0);
                
                //console.log('hoje:' + datahoje.valueOf(), 'data: '+data1.valueOf());
                
                if (datahoje.valueOf() > data1.valueOf()) return 'realizado'
                else return 'aberto'
            },

            formatDate(date){
                if (!date) return null

                const [year, month, day] = date.split('-')
                return `${day}/${month}/${year}`
            }, 

            closeLink(){
                this.dialogLink = false;
                this.link = '';
            },

            copyLink(){
                let copyText = document.getElementById("input-link-convite");

                /* Seleciono o texto do campo */
                copyText.select(); 
                //copyText.setSelectionRange(0, 99999); /*For mobile devices*/

                /* Faço a copia dentro do campo */
                document.execCommand("copy");  
                
                this.snackTitle = 'Link copiado com sucesso, você já pode compartilha-lo!';
                this.snackbar = true;
            },

            // fecho a janela de inclusão/edição de dados
            close(){
                this.dialog = false;
                this.$nextTick(() => {
                    this.meuculto = {};
                    this.tipoOperacao = -1
                });
            },
            // salvo os dados conforme se for inclusão ou edição
            save(){
                if (this.tipoOperacao > -1) {                                
                    this.updateCulto();                    
                } else {
                    this.insertCulto();                    
                }
                this.close()
            },

            // chamo a janela para exibir o link do convite
            showDialogLinkConvite (item) {
                this.meuculto = Object.assign({}, item);
                //console.log('id do link', item.id)
                this.link = window.location.origin + '/agenda/convite/'+ this.meuculto.id;
                this.dialogLink = true;
            },

            // chamo a janela para inclusão ou edição dos dados
            showDialogData(item){
                this.tipoOperacao = this.listacultos.indexOf(item);            
                this.meuculto = Object.assign({}, item);            
                this.dialog = true;
                // console.log('item: ', item);
                // console.log('indice: ', this.tipoOperacao);
                // console.log('item edit: ', this.meuculto);
                //console.log('data computada: ', this.computedDateFormatted);
            },

            deleteData(item){                
                //console.log(item.id);           
                let c = confirm('Você tem certeza de que gostaria de excluir essa informação?') ; 
                if(c == true){                    
                    const index = this.listacultos.indexOf(item)
                    this.deleteCulto(item.id);
                    this.listacultos.splice(index, 1)
                }
            },

            // -----------------------------------------------------
            //    OPERAÇÕES DO CLOUD FIRESTORE
            // -----------------------------------------------------

            // listagem de documento(cultos)
            getListaMeusCultos(){
                this.loading = true;
                db.collection('meuscultos').get()
                    .then(querySnapshot => {
                        this.listacultos.splice(0, this.listacultos.length);
                        querySnapshot.forEach(doc => {
                            this.listacultos.push({...doc.data(), id: doc.id})
                        });
                        this.loading = false;
                    })
            },

            // inclusão de documento (culto)
            insertCulto: function() {
                db.collection('meuscultos').add(this.meuculto)
                    .then(() => {
                        //console.log('incluido com o id: ', docRef.id);
                        this.snackTitle = 'Registro inserido com sucesso!';
                        this.snackbar = true;
                        this.meuculto = {};
                        this.getListaMeusCultos();
                    })
                    .catch(function(error){
                        console.error('Ocorreu um erro ao tentar salvar esse culto!', error);
                    });  
            },

            // atualização de documento (culto)
            updateCulto: function() {
                db.collection('meuscultos').doc(this.meuculto.id).update({
                        culto: this.meuculto.culto,
                        conteudo: this.meuculto.conteudo,
                        data: this.meuculto.data,
                        hora: this.meuculto.hora,
                        vagas: this.meuculto.vagas,
                        usuario_id: this.meuculto.usuario_id
                    })
                    .then(() => {
                        //console.log('dados atualizados com sucesso!')
                        this.snackTitle = 'Registro atualizado com sucesso!';
                        this.snackbar = true;
                        this.meuculto = {};
                        this.getListaMeusCultos();
                    })
                    .catch(function(error){
                        console.error('Ocorreu um erro ao tentar atualizar esse culto!', error);    
                    })
            },

            // exclusão de documento (culto)
            deleteCulto: function(id) {
                db.collection('meuscultos').doc(id).delete()
                    .then(() => {
                        //console.log('Documento selecionado removido!', id);
                        this.snackTitle = 'Registro removido com sucesso!';
                        this.snackbar = true;
                    })
                    .catch(function(error){
                        console.error('Ocorreu um erro ao tentar remover o documento: ', error);
                    });
            }

        },

        created() {
            this.getListaMeusCultos();
        }
    }
</script>

<style lang="scss" scoped>

 /* esse estilo é para colorir o v-chip do status do projeto */
.v-chip.realizado {
  background: green;
}

.v-chip.aberto {
  background:  blue;
}

.v-chip.cancelado {
  background:  red;
}

</style>