<template>
    <div >
        <v-snackbar v-model="snackbar" :timeout="4000" top color="success">
            <span>Seu nome e telefone foram incluídos na lista com sucesso!</span>

            <template v-slot:action="{ attrs }">
                <v-btn elevation="0" text color="white" v-bind="attrs" @click="snackbar = false">Fechar</v-btn>
            </template>
        </v-snackbar>        
        <v-card class="mx-auto" max-width="400">
            <v-img
                class="white--text align-end"
                height="200px"
                src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"                
            >
                <v-card-title>Igreja Assembléia de Deus no Timbó</v-card-title>
            </v-img>

            <v-card-title class="">Culto: {{ this.tituloEvento }} </v-card-title>

            <v-card-text class="text--primary">
                <v-form ref="form">
                    <div><strong>Data:</strong> {{ formatDate(this.dataEvento) }} </div>
                    <div><strong>Hora:</strong> {{ this.horaEvento }} </div>
                    <div><strong>Vagas disponíveis:</strong>  {{ getVagasDisponiveis() }} </div>
                    
                    <div>
                        <v-text-field v-model="nomeConvidado" placeholder="NOME COMPLETO" :rules="rulesFields" required></v-text-field>
                        <div class="v-input v-input--has-state theme--light v-text-field v-text-field--is-booted v-text-field--placeholder error--text">
                            <div class="v-input__control">
                                <div class="v-input__slot">                                     
                                    <div class="v-text-field__slot">
                                        <the-mask v-model="foneConvidado" type="tel" placeholder="TELEFONE" :rules="rulesFields" required mask="(##)#####-####"></the-mask>
                                    </div>
                                </div>
                                <div class="v-text-fields__details">
                                    <div class="v-messages theme--light error--text" role="alert">
                                        <div class="v-messages__wrapper">
                                            <div class="v-messages__message"></div>
                                        </div>
                                    </div>
                                </div>                                    
                            </div>
                        </div>
                        <!-- <v-text-field v-model="foneConvidado" placeholder="TELEFONE" :rules="rulesFields" required return-masked-value mask="(##)#####-####"></v-text-field> -->
                        
                    </div>
                    <v-spacer></v-spacer>
                    <div>
                        
                        <v-btn class="mt-3" large color="success" block :loading="false" @click="salvarConvidadoListaEvento()"> RESERVAR MEU LUGAR </v-btn>                
                    </div>
                </v-form>
            </v-card-text>

            <v-card-actions>
            </v-card-actions>
        </v-card>
    </div>
</template>

<script>    
    import firebase from 'firebase/app'
    import db from '../fb'
    export default {        
        data() {            
            return {
                snackbar: false,
                loading: false,
                nomeFantasia: '',
                tituloEvento: '',
                dataEvento: null,
                horaEvento: null,
                totalVagas: 0,
                eventoId: this.$route.params.eventid,
                usuarioId: '',
                nomeConvidado: '',
                foneConvidado: '',
                totalConvidadosEvento: 0,
                rulesFields: [value => !!value || 'Obrigatório o preenchimento desses campos']
                
            }    
            
        }, 
        
        computed: {
            
        },

        methods: {
            salvarConvidadoListaEvento: function(){
                if(this.$refs.form.validate()){
                    this.loading = true;
                    db.collection('listaconvidados').add({
                        evento_id: this.eventoId,
                        nome_convidado: this.nomeConvidado,
                        fone_convidado: this.foneConvidado
                    })
                    .then(docRef => {
                        console.log('incluido com o id: ', docRef.id); 
                        this.nomeConvidado = '';
                        this.foneConvidado = '';  
                        this.getVagasDisponiveis();
                        this.snackbar = true;
                        this.loading = false;
                    })
                    .catch(function(error){
                        console.error('Ocorreu um erro ao tentar salvar essse convidado nesse evento!', error);
                    });  
                }
            },

            getDadosEvento(){                    
                db.collection('meuscultos').where(firebase.firestore.FieldPath.documentId(), '==', this.$route.params.eventid).get()
                    .then(querySnapshot => {                        
                        querySnapshot.forEach(doc => {
                            console.log(doc.data());
                             this.tituloEvento = doc.data().culto;
                             this.dataEvento = doc.data().data;
                             this.horaEvento = doc.data().hora;
                             this.totalVagas = doc.data().vagas;
                        });
                       
                    })
                    .catch(function(error) {
                        console.error('ocorreu um erro ao tentar buscar esse registro!', error)
                    })
            },

            getTotalConvidadosEvento(){                    
                db.collection('listaconvidados').where('evento_id', '==', this.$route.params.eventid).get()
                    .then(querySnapshot => {  
                        if(querySnapshot.empty){
                            this.totalConvidadosEvento = 0;
                        }else{
                            this.totalConvidadosEvento = querySnapshot.size
                        }
                        console.log('total convidados: ', this.totalConvidadosEvento);

                    })
                    .catch(function(error) {
                        console.error('ocorreu um erro ao tentar buscar esse registro!', error)
                    })
            },

            formatDate(date){
                if (!date) return null

                const [year, month, day] = date.split('-')
                return `${day}/${month}/${year}`
            },

            getVagasDisponiveis() {
                let vagasDisp = 0;
                vagasDisp = this.totalVagas - this.totalConvidadosEvento;
                if(vagasDisp <= 0){
                    return 'vagas totalmente preenchidas';
                }else{
                    return vagasDisp;
                }
            }    
        },

        created() {
            this.getDadosEvento();
            this.getTotalConvidadosEvento();
            this.getVagasDisponiveis();
        }
    }
</script>

