import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import ListaAgenda from '../views/ListaAgenda.vue'
import ListaFielAgenda from '../views/ListaFielAgenda.vue'
import Configuracao from '../views/Configuracao.vue'
import MeusCultos from '../views/MeusCultos.vue'
import Convite from '../views/Convite.vue'

Vue.use(VueRouter)

  const routes = [
  { path: '/login', name: 'Login', component: Login },
  { path: '/listaagenda', name: 'ListaAgenda', component: ListaAgenda },  
  { path: '/convite/:eventid', name: 'Convite', component: Convite },
  { path: '/meuscultos', name: 'MeusCultos', component: MeusCultos },  
  { path: '/config', name: 'Config', component: Configuracao  },
  { path: '/listapresenca/:eventid', name: 'ListaFielAgenda', component: ListaFielAgenda }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
