<template>
    <div>
        <h1 class="subtitle-1 light-blue--text text--darken-4"></h1>
        <v-spacer></v-spacer>
        <v-card class="mx-auto">
            <v-toolbar class="primary white--text" flat>
                <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->
                <v-btn class="white--text" icon router to="/meuscultos">
                    <v-icon>mdi-chevron-left</v-icon>
                </v-btn>

                <v-toolbar-title>Lista de Presença</v-toolbar-title>

                <v-spacer></v-spacer>

                <v-btn class="white--text" icon @click="showSearch()">
                    <v-icon>mdi-magnify</v-icon>
                </v-btn>

                <v-btn icon class="mr-4 white--text" @click="imprimirLista()">
                    <v-icon>mdi-printer</v-icon>
                </v-btn>

               
            </v-toolbar>
            <div class="mx-4" id="lista-presenca">
                <v-card-title>Culto: {{this.tituloEvento}}</v-card-title>
                <v-card-text class="text--primary">
                    <div> <strong>Data:</strong> {{formatDate(this.dataEvento)}} </div>                        
                    <div><strong>Hora:</strong> {{this.horaEvento}} </div>
                    <div><strong>Total de lugares:</strong> {{this.totalVagas}} </div>
                    <v-spacer></v-spacer>
                    <div v-if="showSearchBar == true">
                        <v-text-field v-model="search" append-icon="mdi-magnify" label="Pesquisar" single-line hide-details></v-text-field>                    
                    </div>    
                        
                </v-card-text>
                
                <v-card-text>                                    
                </v-card-text>
                <v-data-table class="mx-4" :headers="headers" :items="listaConvidados" :search="search" :items-per-page="-1" :loading="loading" loading-text="Aguarde, carregando dados." 
                            :footer-props="footerprops">

                    <template v-slot:item.actions="{item}">  
                        <div v-if="showActionOnPrint == true">
                            <v-icon smal class="mr-2" @click="deleteConvidadoNaLista(item)">
                                mdi-delete
                            </v-icon>  
                        </div>
                    </template>
                </v-data-table>
            </div>
        </v-card>    
    </div>

</template>

<script>
    import firebase from 'firebase/app'
    import db from '../fb'
    export default {
        data() {
            return {
                headers: [                        
                        { text: 'Nome', value: 'nome_convidado', class: 'light-blue--text text--darken-4' },
                        { text: 'Telefone', value: 'fone_convidado', class: 'light-blue--text text--darken-4' },
                        { text: 'Acões', value: 'actions', sortable: false, class: 'light-blue--text text--darken-4' }
                        
                    ],
                footerprops: {
                            'items-per-page-all-text': 'tudo',                            
                            'items-per-page-text': 'Pessoas por página',
                            'items-per-page-options': [5, 10, 15, 30, 50, 100, -1]
                        },

                eventoId: this.$route.params.eventid,
                tituloEvento: '',
                dataEvento: null,
                horaEvento: null,
                totalVagas: 0,
                convidado: {
                    nome: '',
                    telefone: ''
                },
                listaConvidados: [],
                loading: false,
                search: '',
                showSearchBar: false,
                showActionOnPrint: true
            }
        },

        methods: {
            getListaConvidadosEvento(){  
                this.loading = true;                  
                db.collection('listaconvidados').where('evento_id', '==', this.$route.params.eventid).get()
                    .then(querySnapshot => {  
                        if(querySnapshot.empty){
                            //this.totalConvidadosEvento = 0;
                            
                        }else{
                            this.listaConvidados.splice(0, this.listaConvidados.length);
                            querySnapshot.forEach(doc => {
                                this.listaConvidados.push({...doc.data(), id: doc.id})
                                console.log('lista de convidados: ', doc.data());
                                
                            });                            
                        }
                        this.loading = false;
                    })
                    .catch(function(error) {
                        console.error('ocorreu um erro ao tentar buscar esse lista!', error)
                    })
            }, 

            getDadosEvento(){                    
                db.collection('meuscultos').where(firebase.firestore.FieldPath.documentId(), '==', this.$route.params.eventid).get()
                    .then(querySnapshot => {                        
                        querySnapshot.forEach(doc => {
                            console.log(doc.data());
                             this.tituloEvento = doc.data().culto;
                             this.dataEvento = doc.data().data;
                             this.horaEvento = doc.data().hora;
                             this.totalVagas = doc.data().vagas;
                        });
                       
                    })
                    .catch(function(error) {
                        console.error('ocorreu um erro ao tentar buscar esse registro!', error)
                    })
            },

             // exclusão de documento (culto)
            deleteConvidadoNaLista: function(item) {                
                let c = confirm('Você tem certeza de que gostaria de excluir essa pessoa da lista de presença?') ; 
                if(c == true){       
                    const index = this.listaConvidados.indexOf(item)             
                    db.collection('listaconvidados').doc(item.id).delete()
                        .then(() => {
                            console.log('Convidado selecionado removido!', item.id);
                        })
                        .catch(function(error){
                            console.error('Ocorreu um erro ao tentar remover o convidado: ', error);
                        });                    
                    this.listaConvidados.splice(index, 1)
                }
            },
            
            imprimirLista(){    
                this.showActionOnPrint = false;            
                window.print();               
                this.showActionOnPrint = true;
            },

            formatDate(date){
                if (!date) return null

                const [year, month, day] = date.split('-')
                return `${day}/${month}/${year}`
            },

            showSearch(){
                this.showSearchBar = !this.showSearchBar;
            }

        },

        created(){
            this.getListaConvidadosEvento();
            this.getDadosEvento();
        }
    }
</script>

