<template>
    <v-dialog v-model="dialog" max-width="600px">
        <template v-slot:activator="{ on }">
            <v-btn small elevation="0" class="light-blue darken-4" v-on="on">
                <span class="caption text-lowercase white--text">Novo culto</span>          
            </v-btn>
        </template>
        <v-card>
            <v-card-title>
                <h2>Criar novo culto</h2>
            </v-card-title>
            <v-card-text>
                <v-form class="px-3">
                    <v-text-field label="Culto" v-model="titulo" color="light-blue darken-4"></v-text-field>        
                    <!-- <v-textarea label="Descrição" v-model="descricao" color="light-blue darken-4"></v-textarea>  -->
                    <!-- <v-select label="Liderança" v-model="lider" :items="lideres"  color="light-blue darken-4" required></v-select>                            -->
                    <v-text-field label="Vagas disponíveis" v-model="vagasdisp" color="light-blue darken-4"></v-text-field>
                    <v-row>
                        <v-col cols="12" sm="6">
                            <v-menu v-model="menudata" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                <template v-slot:activator="{ on }">
                                    <v-text-field :value="computedDateFormatted" label="Data" v-on="on" readonly hint="Formato em DD/MM/YYYY" color="light-blue darken-4"></v-text-field>                            
                                </template>    
                                <v-date-picker v-model="dataevento" no-title @input="menudata = false" locale="pt-br" ></v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-col cols="12" sm="6">
                            <v-text-field label="Hora" v-model="horaevento" type="time" color="light-blue darken-4"></v-text-field>
                        </v-col>
                        
                    </v-row>
                    <v-spacer></v-spacer>
                    <p></p>
                    <v-btn elevation="0" color="light-blue darken-4" v-on:click="submit()">
                        <span class="caption white--text">Gravar dados</span>          
                    </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    data() {
        return{        
            dialog: false,
            menudata: false,
            titulo: '',
            descricao: '', 
            lider: '',  
            vagasdisp: '',         
            dataevento: null,
            horaevento: null,

            lideres: ['Jesus', 'Pedro', 'Tiago', 'Felipe', 'João', 'André', 'Mateus', 'Judas Tadeu', 'Bartolomeu']               
            
        }
    },

    computed: {
        computedDateFormatted () {
            return this.formatDate(this.dataevento)
        }
    },  

    methods:{
        submit(){
            console.log(this.titulo, this.descricao, this.dataevento, this.computedDateFormatted);            
            this.titulo = '';
            this.descricao = '';
            this.dialog = false;
            this.dataevento = null;
            this.lider = '';
            this.vagasdisp = '';
            this.horaevento = null;
            this.$emit('projectAdded');
        },
        formatDate(date){
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${day}/${month}/${year}`
        }
    }   
}
</script>

