<template>  
  <div class="eventos">
    <h1 class="subtitle-1 light-blue--text text--darken-4">Cultos - AD Timbó</h1>

    <v-container class="my-5">
      <!-- botões para indexar por nome e por pessoa -->
      <v-layout row class="mb-3">
        <v-btn small elevation="0" class="grey--text" v-on:click="sortBy('title')">
          <v-icon left small>mdi-folder</v-icon>
          <span class="caption text-lowercase grey--text">Por culto</span>
        </v-btn>
        
        <v-btn small elevation="0" class="grey--text ml-4" v-on:click="sortBy('due')">
          <v-icon left small>mdi-account</v-icon>
          <span class="caption text-lowercase grey--text">Por data</span>
        </v-btn>
        <v-spacer></v-spacer>     

        <FormAgenda @projectAdded="snackbar = true"/>        

      </v-layout>

      <v-card flat color="#FFFFFF" tile elevation="0" v-for="(project, i) in projects" :key="i">
        <v-layout row :class="`pa-3 project ${project.status}`">
          <v-flex xs12 sm6 md4>
            <div class="caption grey--text">Culto</div>
            <div>{{project.title}}</div>
          </v-flex>
          <!-- <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Liderança</div>
            <div>{{project.person}}</div>
          </v-flex> -->
          <v-flex md="auto">
            <div class="caption grey--text">Data</div>
            <div>{{project.due}}</div>
          </v-flex>
          <v-flex md="auto">
            <div class="caption grey--text">Hora</div>
            <div>{{project.time}}</div>
          </v-flex>
          <v-flex md="auto">
            <div class="caption grey--text">Vagas</div>
            <div>{{project.vagasdisp}}</div>
          </v-flex>
          <v-flex md="auto">
            <!-- <div class="caption grey--text">Status</div> -->
            <!-- <div>{{project.status}}</div> -->
            <!-- não esquecer de usar a propriedade color, pois o tema substitui o css customizado abaixo -->
            <div >
                <v-chip small :color="`${project.status}`" :class="`v-chip--active white--text caption my-2`">{{project.status}}</v-chip>
            </div>            
          </v-flex>
          <v-flex md="auto">
            <div class="caption grey--text">Ação</div>
            <div >
              <v-btn small tile elevation="0" class="light-blue darken-4 white--text">
                <v-icon class="">mdi-pencil</v-icon>    
              </v-btn>
              <v-btn small tile elevation="0" color="error">
                <v-icon>mdi-trash-can</v-icon>    
              </v-btn>
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
      </v-card>      
      
    </v-container>
  </div>
</template>

<script>
import FormAgenda from '../components/FormAgenda'


export default {
  components: { FormAgenda }, 
  data() {
    return {      
      projects: [
        { title: 'Eu sou o caminho', person: 'Jesus', due: '1 Jan 2019', time: '18:30', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Dev full stack', person: 'Jefferson', due: '13 Mar 2019', time: '20:30', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Oração forte', person: 'Antonio', due: '25 Feb 2019', time: '16:00', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Vida em Cristo', person: 'Mateus', due: '13 Feb 2019', time: '18:30', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Jejum de 10 dias', person: 'Bartolomeu', due: '20 Dez 2019', time: '08:30', vagasdisp: '100', status: 'cancelado', content: ''},
        { title: 'Igreja Santa do Timbó', person: 'Paulo', due: '8 Jun 2020', time: '15:00', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Espírito de Luz', person: 'Felipe', due: '28 Aug 2019', time: '21:00', vagasdisp: '100', status: 'realizado', content: ''},
        { title: 'Na paz do senhor', person: 'Tiago', due: '13 Jun 2020', time: '18:30', vagasdisp: '100', status: 'aberto', content: ''}

      ]
    }  
  },
  methods: {
    sortBy(prop){
      this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1) 
    }
    
  }

}
</script>

<style>

/* esse estilo é para colocar uma borda na lateral esquerda do card */
.project.realizado {
  border-left: 4px solid #05a528;
}

.project.aberto {
  border-left: 4px solid rgb(28, 126, 238);
}

.project.cancelado {
  border-left: 4px solid rgb(206, 49, 21);
}

 /* esse estilo é para colorir o v-chip do status do projeto */
.v-chip.realizado {
  background: #05a528;
}

.v-chip.aberto {
  background: rgb(28, 126, 238);
}

.v-chip.cancelado {
  background:  rgb(206, 49, 21);
}
</style>
